.works{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;
}
/* right side */
.w-right{
    position: relative;
}
.w-maincircle{
    left: 9rem;
    position: relative;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top: 2rem;
    background: white;
}
.w-maincircle>*{
    position: absolute;
}
.w-maincircle > :nth-child(1){
    top: -3rem;
    left: 6rem;
}
.w-maincircle > :nth-child(2){
    left: -3rem;
    top: 5rem;
}
.w-maincircle > :nth-child(3){
    left: 6rem;
    top: 5rem;
}
.w-maincircle > :nth-child(4){
    left: 15rem;
    top: 5rem;
}
.w-maincircle > :nth-child(5){
    left: 6rem;
    top: 13rem;
}
.w-seccircle{
    width: 6rem;
    height: 6rem;
    position: absolute;
    border-radius: 100%; 
    border: 5px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    box-shadow: var(--smboxShadow);
    background: white;
}
.w-seccircle{
    transform: scale(0.6);
}
.w-backcircle{
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    z-index: -1;
}
.bluecircle{
    background: #1949B8;
    left: 18rem;
    top: 0rem;
}
.yellowcircle{
    background: #F5C32C;
    left: 18rem;
    top: 8rem;
}
@media screen and (max-width: 480px) {
        .works{
            flex-direction: column;
            height: 55rem;
            padding: 0;
            gap: 5rem;
        }
        .w-right{
            display: flex;
            align-items: flex-start;
            justify-content: start;
            transform: scale(0.7);
        }
        .w-maincircle{
            position: static;
        }
        .w-backcircle{
            left: 8rem !important;
        }

}